pre, code {
  font-family: Consolas, 'Courier New', Courier, Monaco, monospace;
  font-size: 1.2rem;
  line-height: 1;
  tab-size: 4;
}

pre {
  background-color: #333;
  padding: 1rem;
}

pre code {
  color: #fff;
}

section {
  padding-left: 1rem;
  padding-right: 1rem;
}

section + section {
  margin-top: 100px;
  padding-top: 100px;
  border-top: 4px solid #ddd;
}

h1 {
  font-size: 3rem;
  padding: 1.5rem;
}

h2 {
  font-size: 2.5rem;
  padding-left: 1rem;
  margin: 1rem 0px 2rem;
}

h2 + p {
  padding-left: 1rem;
  font-size: 1.3rem;
  letter-spacing: 0.1rem;
  line-height: 1.5;
}

.block {
  box-sizing: border-box;
  float: left;
  padding: 2rem;
  border: 2px solid #ddd;
  margin: -2px 0 0 -2px;
  flex: 1 calc(100% / 1);
}

.block__wrapper {
  margin-top: 2rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

.block__wrapper:after {
  display: block;
  clear: both;
  content: '';
}

@media (min-width: 600px) {
  .block {
    flex: 1 calc(100% / 2);
  }
}

@media (min-width: 900px) {
  .block {
    flex: 1 calc(100% / 3);
  }
}

@media (min-width: 1500px) {
  .block {
    flex: 1 calc(100% / 4);
  }
}

@media (min-width: 1900px) {
  .block {
    flex: 1 calc(100% / 5);
  }
}

.block__view {
  margin-bottom: 2rem;
}

.block__comment {
  font-size: 1.2rem;
  line-height: 1.3;
  margin-bottom: 1rem;
}
